import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { HttpHeaders } from '@angular/common/http';
import { StudentService } from '../service/student.service';
import { AddStudentDialogComponent } from '../add-student-dialog/add-student-dialog.component';

@Component({
    selector: 'app-student-list',
    templateUrl: './student-list.component.html',
    styleUrls: ['./student-list.component.scss'],
})
export class StudentListComponent implements OnInit {
    displayedColumns: string[] = ['select', 'rollNo', 'name', 'standard', 'address', 'dateOfBirth', 'phone'];
    dataSource: any[] = [];
    selection = new SelectionModel<any>(true, []);

    requestHeader = new HttpHeaders({
        'Content-Type': 'application/json',
    });

    constructor(private studentService: StudentService, private dialog: MatDialog) {
      this.getAllStudents();
    }

    ngOnInit(): void {
      // this.getAllStudents();
    }

    /** Whether the number of selected elements matches the total number of rows. */
    isAllSelected() {
        const numSelected = this.selection.selected.length;
        const numRows = this.dataSource.length;
        return numSelected === numRows;
    }

    /** Selects all rows if they are not all selected; otherwise clear selection. */
    masterToggle() {
        this.isAllSelected() ? this.selection.clear() : this.dataSource.forEach((row) => this.selection.select(row));
    }

    /** The label for the checkbox on the passed row */
    checkboxLabel(row?: any): string {
        if (!row) {
            return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
        }
        return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
    }


    getAllStudents() {
      this.selection.clear();
      this.studentService.getAllStudents().subscribe(
          (response: any[]) => {
              if (response) {
                  this.dataSource = response;
              } else {
                this.dataSource = [];
              }
          }, (error) => {
              this.dataSource = [];
              console.log(error);
          }
      );
  }

    deleteStudent(student?) {
        let studentIdArr = [];
        let id='';
        if (!student) {
            this.selection.selected.forEach((row) => {
                studentIdArr.push({ id: row['id'] });
                id = row['id'];
            });
        } else {
            studentIdArr.push({ componentId: student['id'] });
        }

        // const options = {
        //     headers: this.requestHeader,
        //     body: studentIdArr,
        // };

        this.studentService.deleteStudent(id).subscribe(
            (response) => {
                // this.toastr.success('Component deleted successfully.', 'Success');
                this.getAllStudents();
            },
            (error) => {
              console.log(error);
                // this.toastr.error('Internal server error.', 'Error');
            }
        );
    }

    addStudent(student?) {
      const dialogRef = this.dialog.open(AddStudentDialogComponent, {
          height: '100vh',
          width: '25%',
          position: { top: '0', right: '0' },
          data: student,
      });

      dialogRef.beforeClosed().subscribe((data) => {
          if (data && data.isRefreshList) {
              this.getAllStudents();
          }
      });
  }

  editStudent(student?) {
      if (!student) {
        student = this.selection.selected[0];
      }

      this.addStudent(student);
  }

}
